import axios from 'axios'

export default {
  strict: true,
  namespaced: true,
  state: {
    products: [],
    product: {},
    productId: '',
    categories: [],
    searchText: '',
    searchCat: '',
    searchSubCat: '',
    filterProducts: []
  },
  actions: {
    getAllProducts (context, page) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`
      context.commit('LOADING', true, {root: true})
      axios.get(api).then((response) => {
        context.commit('PRODUCTS', response.data.products)
        context.commit('CATEGORIES', response.data.products)
        context.commit('LOADING', false, {root: true})
      })
    },
    getClientAllProducts (context) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`
      context.commit('LOADING', true, {root: true})
      axios.get(api).then((response) => {
        response.data.products.forEach((item) => {
          let newCategory = item.category.split('/')
          item.category = newCategory
        })
        context.commit('PRODUCTS', response.data.products)
        context.commit('CATEGORIES', response.data.products)
        context.commit('LOADING', false, {root: true})
      })
    },
    getProduct (context, id) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`
      context.commit('LOADING', true, {root: true})
      axios.get(api).then((response) => {
        response.data.product.num = 1
        context.commit('PRODUCT', response.data.product)
        context.commit('LOADING', false, {root: true})
      })
    }
  },
  mutations: {
    PRODUCTS (state, payload) {
      state.products = payload
    },
    PRODUCT (state, payload) {
      state.product = payload
    },
    PRODUCT_ID (state, payload) {
      state.productId = payload
    },
    CATEGORIES (state, payload) {
      payload.forEach((item) => {
        state.categories.push(item.category)
      })
    },
    SEARCH_TEXT (state, payload) {
      state.searchText = payload
    },
    SEARCH_CAT (state, payload) {
      state.searchCat = payload
    },
    SEARCH_SUBCAT (state, payload) {
      state.searchSubCat = payload
    }
  },
  getters: {
    products: state => state.products,
    product: state => state.product,
    productId: state => state.productId,
    categories: state => state.categories,
    searchText: state => state.searchText,
    searchCat: state => state.searchCat,
    searchSubCat: state => state.searchSubCat,
    searchProducts: (state) => {
      return state.products.filter(item => {
        if (item.title.indexOf(state.searchText) !== -1) {
          const data = item
          return data
        }
      })
    },
    filterProducts: (state) => {
      if (state.products) {
        return state.products.filter((item) => {
          if (item.category.indexOf(state.searchCat) !== -1) {
            if (state.searchSubCat !== '') {
              if (item.category.indexOf(state.searchSubCat) !== -1) {
                const data = item
                return data
              }
            } else {
              const data = item
              return data
            }
          }
        })
      }
    }
  }
}



